//IntroCover 
        .About-Intro {
            width: 100%;
            height: 100vh;
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            // opacity: 0;
            z-index: 600;
        }
            .About-Intro-Wrap {
                width: 100%;
                height: 100%;
                position: relative;
            }  

                .About-IntroSlide {
                    position: absolute;
                    width: 50%;
                    height: 100%;
                    top: 0;
                    bottom: 0;
                    background-color: rgb(240, 240, 240);
                    transition: all;
                    z-index: 1;
                }
                    .About-IntroSlideLeft {
                        left: -100%;
                    }

                    .About-IntroSlideRight {
                        right: -100%;
                    }




.About {
    width: 100%;
    background-color: #141414;
}

    .AboutWrapper {
        width: 100%;
        height: 100%;
        padding: 0 10%;
        position: relative;
    }

        .AboutTitle {
            margin-top: 100px;
            font-size: 75px;
            font-weight: 900;
            font-family: aktiv-grotesk-condensed, sans-serif;
            text-transform: uppercase;
            transition: all .5s ease-in-out;
        }
            .AboutTitleThin {
                font-size: 75px;
                font-weight: 200;
                font-family: aktiv-grotesk-condensed, sans-serif;
                text-transform: uppercase;
                margin-bottom: 80px;
                transition: all .5s ease-in-out;
            }


        .AboutContentWrapper {
            width: 600px;
            padding-left: 10%;
            margin-bottom: 150px;
            
        }

            .AboutSection {
                width: 100%;
                margin-bottom: 110px;
                // transition: all .5s ease-in-out;
            }

                .AboutSectionTitle {
                    font-size: 40px;
                    font-weight: 900;
                    font-family: aktiv-grotesk-condensed, sans-serif;
                    text-transform: uppercase;
                    margin-bottom: 20px;
                    transition: font-size .5s ease-in-out;
                }

                    .AboutSectionSubtitle {
                        font-size: 20px;
                        margin-bottom: 10px;

                    }

                        .AboutSectionSubtitle2 {
                            margin-top: 40px;
                        }

                    .AboutSectionText {
                        font-size: 16px;
                        line-height: 30px;
                    }
                        .AboutSectionTextDark {
                            color: #828282;
                        }

        .FooterAbout {
            position: relative;
            padding-left: 0;
        }


@media screen and (max-width: 760px){
    .AboutSection {
        margin-bottom: 70px;
    }
    .AboutContentWrapper {
        width: 100%;
        padding-left: 5%;
        transition: all .5s ease-in-out;
    }
    .AboutTitle {
        font-size: 50px;
    }
    .AboutTitleThin {
        font-size: 50px;
    }
    .AboutSectionTitle {
        font-size: 24px;
    }
    .AboutSectionSubtitle {
        font-size: 18px;
    }
    .AboutSectionText {
        font-size: 12px;
    }
    .AboutSectionTextDark {
        font-size: 14px;
    }

}